*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: normal;
}

a, span {
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img, video, embed, object {
  max-width: 100%;
}

img {
  vertical-align: middle;
}

label, button {
  cursor: pointer;
}

textarea, input[type] {
  background: transparent;
  color: #FFF;
  font-size: em(25);
  font-weight: bold;
  padding: 20px;

  &:focus {
    background: transparent;
    outline: none;
  }
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #FFF;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #FFF;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #FFF;
}
:-moz-placeholder { /* Firefox 18- */
  color: #FFF;
}
