html {
  background-color: $color-secondary;
}

body {
  background: #FFF;
  opacity: 1;
  transition: 1s opacity;
}

body.fade-out {
    opacity: 0;
    transition: none;
}

html, body {
  color: #000;
  font-size: 16px;
}

body, input, select, textarea, button {
  font-family: 'Lato', sans-serif;
  font-weight: lighter;
  font-size: 100%;
}

.page {
  background: #000;
}
