//
//-- Footer
//

.page-footer {
  background: #fc4f1e;
  padding: em(40) 0;
  margin-left: 55px;
}

.page-footer__title {
  font-size: em(17);
  font-weight: bold;
  color: #FFF;
  text-align: center;

  @include tablet-port {
    font-size: em(22)
  };

  @include tablet-land {
    font-size: em(25)
  };
}

.page-footer__content {
  margin: 0 auto;
  text-align: center;
}

.page-footer__item {
  display: inline-block;
  font-size: em(20);
  font-weight: bold;
  color: #000;
  padding-right: 1em;
  text-transform: capitalize;
  margin-top: .7em;

  @include tablet-port {
    font-size: em(25)
  };

  @include tablet-land {
    font-size: em(27)
  };
}

.page-footer__copyright {
  font-size: em(14);
  font-weight: 300;
  color: #FFF;
  text-align: center;
  margin-top: 1.5em;
}
