//
//-- Contact
//

.contact {
  // background: $color-secondary;
}

.contact__wrapper {
  max-width: em(978);
  padding: em(70) ;
  margin: 0 auto;
}

.contact__intro-text {
  font-weight: bold;
  padding-left: 2em;
  padding-right: 2em;
  padding-top: 1em;
}

.contact-form-alt {
  display: block;
  text-align: center;
  font-weight: bold;

  @include tablet-port {
    display: none;
  };
}

.contact-form-alt__text {
  font-size: em(23);
  color: #FFF;
  font-weight: bold;
}

.contact-form-alt___email {
  display: block;
  color: $color-base;
  border: 2px solid $color-base;
  width: em(120);
  margin: 1em auto;
  padding: 0.5em;

  &:active {
    background: transparent;
  }
}

.contact-form {
  display: none;
  font-size: em(18);
  // width: 25em;
  margin: 0 auto;

  @include tablet-port {
    display: block;
    width: 30em;
  };

  @include tablet-land {
    display: block;
    width: 40em;
  };
}

.contact-form__container {
  margin-bottom: 1em;
}

.contact-form__field {
  border: 2px solid $color-base;
  margin-bottom: 0.5em;
  width: 100%;

  // remove Safari's rounded corners
  border-radius: 0;
  -webkit-appearance: none;

  @include tablet-land {
    width: 49%;
    margin-bottom: 0.25em;

    &:first-child {
      margin-right: 1.5%;
    }
  };
}

.contact-form__field--message {
  height: 8em;
  margin-bottom: 2em;
  width: 100%;
  font-weight: bold;
  font-size: em(25);
}

.contact-form__button {
  font-size: em(18);
  font-weight: bold;
  border: 2px solid #000;
  color: #000;
  text-transform: capitalize;
  padding: 1em;
  background: transparent;
}
